
<template>

    <div>
        <el-tag type="warning"  style="margin-left: 20px;margin-bottom: 10px;width: 200px;height: 40px;text-align: center;line-height: 40px;font-size: 20px">行程耗时</el-tag>
        <el-col :span="23" style=" padding-bottom: 30px;">
            <el-row>
                <el-col :span="4" class="el-col"  style="margin-left: 0">
                    <p class="input-title">出发地:</p>
                    <el-input  v-model="searchForm.depStation" placeholder="请输入出发地">
                    </el-input>
                </el-col>
                <el-col :span="4" class="el-col">
                    <p class="input-title">到达地:</p>
                    <el-input  v-model="searchForm.destStation" placeholder="请输入到达地">
                    </el-input>
                </el-col>
                <el-col :span="1.5" class="el-col" style="">
                    <p class="input-title">&nbsp;</p>
                    <el-button type="primary" icon="el-icon-search" @click = "search">查询</el-button>
                </el-col>
                <el-col :span="1.5" class="el-col" style="">
                    <p class="input-title">&nbsp;</p>
                    <el-button type="primary" icon="el-icon-plus"  @click = "addruntime">新增</el-button>
                </el-col>

            </el-row>
        </el-col>

        <el-col :span="23" style=" padding-top: 30px;">
            <table class="content-table">
                <tr>
                    <th>ID</th>
                    <th>出发地</th>
                    <th>到达地</th>
                    <th>耗时</th>
                    <th>操作</th>
                </tr>
                <tr v-for="(item,index) in aaData.data">
                    <td>{{item.id}}</td>
                    <td>{{item.depStation}}</td>
                    <td>{{item.destStation}}</td>
                    <td>{{item.runTime }}</td>
                    <td>
                        <span class="dothis" @click="edit(item.id,item.supplierDepId,item.supplierDestId,item.runTimeHour,item.runTimeMinute,item.depStation)">编辑</span>
                        <span class="dothis" @click="del(item.id)">删除</span>
                    </td>
                </tr>
            </table>
            <div class="block page-f" style="position: relative;width: 100%">

                <el-pagination
                    style="position: absolute;right: 0"
                    background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage"
                    :page-size="pageSize"
                    :page-sizes="[10, 20, 30, 40]"
                    layout="total,prev, pager, next, jumper"
                    :total="aaData.totalCount">
                </el-pagination>
            </div>
            <div style="height: 80px"></div>
        </el-col>

        <el-dialog title="新增行程" :visible.sync="addvisible">
            <el-form :model="addForm">
                <el-form-item label="出发地" :label-width="formLabelWidth">
                    <el-select  v-model="addForm.supplierDepId"  placeholder="请选择出发地" @change="depChange"  style="width: 400px">
                        <el-option
                            v-for="item in DepOption"
                            :key="item.depStationCode"
                            :label="item.depStation"
                            :value="item.depStationCode">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="到达地" :label-width="formLabelWidth">
                    <el-select  v-model="addForm.supplierDestId"  placeholder="请选择到达地(先选择出发地)" @change="destChange"  style="width: 400px">
                        <el-option
                            v-for="item in DestOption"
                            :key="item.destStationCode"
                            :label="item.destStation"
                            :value="item.destStationCode">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="耗时" :label-width="formLabelWidth" >
                    <el-input type="number" v-model="addForm.runTimeHour" auto-complete="off " style="width: 150px" placeholder="请输入小时数"></el-input>

                    时
                    &nbsp; &nbsp; &nbsp;
                    <el-input type="number" v-model="addForm.runTimeMinute" auto-complete="off " style="width: 150px" placeholder="请输入分钟数"></el-input>
                    分
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addvisible = false">取 消</el-button>
                <el-button type="primary" @click="addsure" >确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog title="编辑行程" :visible.sync="editvisible">
            <el-form :model="editForm">
                <el-form-item label="出发地" :label-width="formLabelWidth">
                    <el-select  v-model="editForm.supplierDepId"  placeholder="请选择出发地" @change="depChange"  style="width: 400px">
                        <el-option
                            v-for="item in DepOption"
                            :key="item.depStationCode"
                            :label="item.depStation"
                            :value="item.depStationCode">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="到达地" :label-width="formLabelWidth">
                    <el-select  v-model="editForm.supplierDestId"  placeholder="请选择到达地(先选择出发地)" @change="destChange"  style="width: 400px">
                        <el-option
                            v-for="item in DestOption"
                            :key="item.destStationCode"
                            :label="item.destStation"
                            :value="item.destStationCode">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="耗时" :label-width="formLabelWidth" >
                    <el-input type="number" v-model="editForm.runTimeHour" auto-complete="off " style="width: 150px" placeholder="请输入小时数"></el-input>时 &nbsp; &nbsp; &nbsp;
                    <el-input type="number" v-model="editForm.runTimeMinute" auto-complete="off " style="width: 150px" placeholder="请输入分钟数"></el-input>分
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="editvisible = false">取 消</el-button>
                <el-button type="primary" @click="editsure" >确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
    import ajax from "../model/sys/index"
    export default {
        data() {
            return {
                value:'',
                currentPage: 1,//默认页码
                pageSize:10,//每页显示条数
                totalNum:10,//总条数
                aaData:[],
                addvisible:false,
                editvisible:false,
                from:"",
                to:"",
                searchForm:{
                    depStation:"",
                    destStation:""
                },
                addForm: {
                    supplierDepId:"",
                    supplierDestId:"",
                    runTimeHour:"",
                    runTimeMinute:"",
                },
                editForm: {
                    supplierDepId:"",
                    supplierDestId:"",
                    runTimeHour:"",
                    runTimeMinute:"",
                    id:""
                },
                formLabelWidth: '120px',
                DepOption:"",
                DestOption:""
            };
        },
        methods: {


            getRunTimeList:function () {
                var data  = this.searchForm
                data.pageIndex=this.currentPage
                data.pageSize=this.pageSize

                for(var i in data){
                    if(String(data[i])==""){
                        delete data[i]
                    }
                }

                ajax.runTimeList(data,res=>{
                    if(res){
                        this.aaData=res.data.result
                    }
                })
            },
            transfer(){

            },
            addruntime(){
                this.addvisible = true
                for(var i in this.addForm){
                    this.addForm[i]=""
                }
                this.DestOption=[]
            },
            addsure(){

                for(var i in this.addForm){
                    if(String(this.addForm[i])==""){
                        this.$message({
                            showClose: true,
                            message: '所有输入框为必填项',
                            type: 'fail'
                        });
                        return
                    }
                }

                ajax.runTimeAdd(this.addForm,res=>{
                    if(res){
                        this.addvisible = false
                        this.getRunTimeList()
                    }
                })
            },

            search:function () {
                this.currentPage = 1
                this.getRunTimeList ()

            },
            edit(id,supplierDepId,supplierDestId,runTimeHour,runTimeMinute,name){
                this.editvisible=true
                this.editForm={
                    supplierDepId:supplierDepId+"",
                    supplierDestId:supplierDestId+"",
                    runTimeHour:runTimeHour,
                    runTimeMinute:runTimeMinute,
                    id:id
                }
                var destData = {
                    depStationCode:supplierDepId,
                    depStationName:name
                }
                this.getDestList(destData)
            },
            editsure(){
                for(var i in this.editForm){
                    if(String(this.editForm[i])==""){
                        this.$message({
                            showClose: true,
                            message: '所有输入框为必填项',
                            type: 'fail'
                        });
                        return
                    }
                }
                ajax.runTimeUpdate(this.editForm,res=>{
                    if(res){
                        this.editvisible=false
                        this.getRunTimeList()
                        this.$message({
                            showClose: true,
                            message: '更新成功',
                            type: 'success'
                        });
                    }
                })
            },
            del(id){
                this.$confirm('是否继续删除操作?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    ajax.runTimeDelete({id:id},res=>{
                        if(res){
                            this.getRunTimeList()
                            this.$message({
                                showClose: true,
                                message: '删除成功',
                                type: 'success'
                            });
                        }
                    })

                }).catch(() => {

                });


            },

            getDepList(){
                ajax.getDepList({vehicleType:1},res=>{
                    if(res){
                        this.DepOption = res.data.result
                    }

                })
            },
            depChange(a){
                var datas={}
                for(var i=0;i<this.DepOption.length;i++){
                    if(this.DepOption[i].depStationCode==a){
                        datas.depStationCode=a
                        datas.depStationName=this.DepOption[i].depStation
                    }
                }
                this.getDestList(datas)
            },
            destChange(){
                console.log("000")
            },
            getDestList(param){
                ajax.getDestList(param,res=>{
                    if(res){
                        this.DestOption = res.data.result
                    }

                })
            },
            handleSizeChange(val) {
                this.pageSize = val
                this.getRunTimeList ()
            },
            handleCurrentChange(val) {
                this.currentPage = val
                this.getRunTimeList ()
            }
        },
        mounted(){
            this.getDepList()
            this.getRunTimeList()
         }
    }
</script>
<style>
    .input-title{
        font-size: 14px !important;
        font-weight: 600;
    }
    .el-col{
        margin-left: 20px;
    }
    ::-webkit-scrollbar-thumb{
        background-color: gainsboro;
    }
    .cell{
        text-align: center;
    }
    #content-main{
        overflow: scroll;
    }
    .el-table{
        overflow: scroll;
    }
    .content-table{
        width: 100%;
        border-collapse:collapse;
        background-color: white;
        text-align: center;
    }
    .content-table th{
        height: 40px;
        border: 1px solid #F2F3F4;
        text-align: center;
        background-color: gainsboro;
    }
    .content-table td{
        height: 40px;
        border: 1px solid #F2F3F4;
    }
    .dothis{
        color: #3C9CFC;
        cursor:default;
        margin-left: 10px;
    }
    .page-f{
        margin-top: 20px;
        float: right;
    }
    .content-table span:hover{
        color: silver;
    }
    .el-col-23{
        width: 99% !important;
    }
</style>



